import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Subscription } from 'rxjs';
import { AuthService } from 'src/app/pages/auth/auth.service';

@Component({
  selector: 'app-side-menu',
  templateUrl: './side-menu.component.html',
  styleUrls: ['./side-menu.component.scss'],
})
export class SideMenuComponent implements OnInit {
  private userSub: Subscription;
  @Output() closeSide = new EventEmitter();

  constructor(private authService: AuthService) {}

  ngOnInit() {
    this.userSub = this.authService.user.subscribe((user) => {
      if (user.admin) {
        this.items = [
          {
            isParent: 'n',
            name: 'Dashboard',
            icon: 'trending_up',
            route: '/',
          },

          {
            isParent: 'y',
            name: 'Products',
            children: [
              {
                name: 'Product attributes',
                icon: 'trending_up',
                route: '/product-attributes',
              },
              {
                name: 'Categories',
                icon: 'trending_up',
                route: '/categories',
              },
              {
                name: 'Products',
                icon: 'trending_up',
                route: '/products',
              },
            ],
          },

          {
            isParent: 'y',
            name: 'Stores',
            children: [
              {
                name: 'Stores',
                icon: 'trending_up',
                route: '/stores',
              },
              {
                name: 'Owners',
                icon: 'person',
                route: '/owners',
              },
            ],
          },

          {
            isParent: 'y',
            name: 'Groups',
            children: [
              {
                name: 'Groups',
                icon: 'trending_up',
                route: '/groups',
              },
              {
                name: 'Presidents',
                icon: 'person',
                route: '/leaders',
              },
            ],
          },

          {
            isParent: 'n',
            name: 'Users',
            icon: 'person',
            route: '/users',
          },
          {
            isParent: 'n',
            name: 'Events',
            icon: 'trending_up',
            route: '/events',
          },
          {
            isParent: 'n',
            name: 'Shipping',
            icon: 'trending_up',
            route: '/shippers',
          },
          {
            isParent: 'n',
            name: 'Orders',
            icon: 'trending_up',
            route: '/orders',
          },
          {
            isParent: 'n',
            name: 'Ads',
            icon: 'trending_up',
            route: '/ads',
          },
        ];
      } else if (user.owner) {
        this.items = [
          {
            isParent: 'n',
            name: 'Dashboard',
            icon: 'trending_up',
            route: '/',
          },

          {
            isParent: 'n',
            name: 'Products',
            icon: 'trending_up',
            route: '/products',
          },
          {
            isParent: 'n',
            name: 'Orders',
            icon: 'trending_up',
            route: '/orders',
          },
        ];
      }
    });
  }
  ngOnDestroy(): void {
    this.userSub.unsubscribe();
  }

  items = [];

  closeMenu() {
    this.closeSide.emit();
  }
}
